:root {
  --purple:   #3c1361;
  --light-bg: #f5f5f5;
  --white:    #fff;
  --grey:     #ccc;
  --green:    #00e676;
  --orange:   #ff5722;
  --dark-grey:    #333;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', sans-serif;
}

.app {
  background: var(--light-bg);
  min-height: 100vh;
}

/* Header */
.header {
  background-color: var(--purple);
  color: var(--white);
  padding: 1.5rem 1rem;
  height: 24vh;
}
.header-inner {
  max-width: 400px;
  margin: 0 auto;
}
.header h2 {
  margin: 0;
  font-weight: normal;
  opacity: 0.8;
}

.balance {
  margin: 1rem 0;
}
.balance span {
  display: block;
  opacity: 0.7;
}
.balance h1 {
  margin: 0;
  font-size: 2rem;
}

/* Chart */
.chart-small {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.chart-small svg {
  width: 80px;
  height: 80px;
}
.bg-ring {
  fill: none;
  stroke: var(--white);
  opacity: 0.3;
}
.ring-expense {
  fill: none;
  stroke: var(--orange);
}
.ring-income {
  fill: none;
  stroke: var(--green);
}

.labels {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.labels div {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.labels small {
  opacity: 0.7;
}
.labels strong {
  margin-left: auto;
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  display: inline-block;
}
.income-dot { background-color: var(--green); }
.expense-dot { background-color: var(--orange); }

/* Dashboard */
.dashboard {
  height: 58vh;
  background-color: var(--white);
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  margin: -1.5rem auto 0;
  max-width: 400px;
  padding: 1rem;
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.05);
}

/* Tabs */
.tabs {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
}
.tabs button {
  background: none;
  border: none;
  padding: 0.5rem;
  font-size: 1rem;
  opacity: 0.6;
  cursor: pointer;
}
.tabs button.active {
  opacity: 1;
  border-bottom: 2px solid var(--purple);
}

/* List */
.list {
  list-style: none;
  padding: 0;
  max-height: 150px;
  overflow-y: auto;
  margin-bottom: 1rem;
}
.list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  border-bottom: 1px solid var(--grey);
}
.list li.income span:last-child {
  color: var(--green);
}
.list li.expense span:last-child {
  color: var(--orange);
}

.actions button {
  background: none;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  opacity: 0.7;
  padding: 0 0.25rem;
}
.actions button:hover { opacity: 1; }

/* Form */
.form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  gap: 0.5rem;
  width: 400px;
  margin: 0 auto;
  background-color: var(--white);
}
.form input,
.form select {
  flex: 1;
  padding: 0.5rem;
  border: 1px solid var(--grey);
  border-radius: 4px;
}
.form button {
  padding: 0.5rem 1rem;
  border: none;
  background-color: var(--purple);
  color: var(--white);
  border-radius: 4px;
  cursor: pointer;
}